import React, { ChangeEvent, FormEvent } from 'react';
import styles from './styles.module.css';
import Team from '../../models/Team';
import { ErrorsContext } from '../App/App';
import { ErrorHandler } from '../../models/Handlers';
import { sendGetRequest } from '../../utils/sendRequest';
import { currentTeam } from '../../consts/api';
import { ROUTES } from '../../consts/routes';

type LoginPageProps = {
    history: {
        push: (url: string) => void;
    };
    updateTeam: (team: Team) => void;
}

type LoginPageState = {
    apiKey: string;
    loading: boolean;
}

export default class LoginPage extends React.Component<LoginPageProps, LoginPageState> {
    constructor(props: LoginPageProps) {
        super(props);
        this.state = {
            apiKey: '',
            loading: false,
        };
    }

    render() {
        const { loading } = this.state;
        return (
            <div>
                <h2>Team Log In</h2>
                <ErrorsContext.Consumer>{
                    ({ addError }) =>
                        <form className={ styles.form } onSubmit={ this.onSubmit(addError) }>
                            <label className={ styles.row }>
                                <span>API key</span>
                                <input name='apiKey'
                                       required
                                       autoFocus
                                       onChange={ this.onChange }
                                />
                            </label>
                            <label className={ styles.buttonRow }>
                                <span/>
                                <div>
                                    <button type='submit' disabled={loading}>
                                        { loading ? "Logging In..." : "Log In" }
                                    </button>
                                </div>
                            </label>
                        </form>
                }
                </ErrorsContext.Consumer>
            </div>
        )
    }

    onChange = (e: ChangeEvent<HTMLInputElement>) => {
        this.setState({ apiKey: e.target.value })
    }

    onSubmit = (errorHandler: ErrorHandler) => async (e: FormEvent) => {
        e.preventDefault();
        const { apiKey } = this.state;

        this.setState({ loading: true })
        try {
            const res = await sendGetRequest(`${ currentTeam }?apiKey=${ apiKey }`, errorHandler, true, false);
            console.log(res);
            this.props.updateTeam(res);
            this.props.history.push(ROUTES.teamInfo);
        } catch (e) {
            errorHandler(e);
        } finally {
            this.setState({ loading: false })
        }
    }
}
