import React, { createContext } from 'react';
import { HashRouter as Router } from 'react-router-dom';
import styles from './styles.module.css';
import Header from '../Header';
import Team from '../../models/Team';
import { API_KEY } from '../../consts/strings';
import { sendGetRequest } from '../../utils/sendRequest';
import { currentTeam } from '../../consts/api';
import Error from '../Error'
import AppRouter from './AppRouter';

export const ErrorsContext = createContext({
    addError: (err: Error | string) => {
        /* do nothing */
    }
})

type AppState = {
    apiKey: string | null;
    team: Team | null;
    error: Error | string | null;
    teamLoading: boolean;
}

class App extends React.Component<{}, AppState> {
    constructor(props: {}) {
        super(props);

        let apiKey;

        try {
            apiKey = window.localStorage.getItem(API_KEY);
        } catch (e) {
            /*...*/
        }

        this.state = {
            apiKey: apiKey || null,
            team: null,
            error: null,
            teamLoading: Boolean(apiKey),
        }
    }

    componentDidMount() {
        const { apiKey } = this.state;
        if (!apiKey) return;

        sendGetRequest(currentTeam, this.setError).then(res => {
            this.setState({ team: res });
        }, this.setError).finally(() => this.setState({ teamLoading: false }))
    }

    render() {
        const { error, team, teamLoading } = this.state;
        return (
            <ErrorsContext.Provider value={ ({ addError: this.setError }) }>
                <div className={ styles.container }>
                    <Router>
                        <Header team={ team }
                                isTeamLoading={ teamLoading }
                                logout={ this.deleteApiKey }/>
                        <AppRouter updateTeam={ this.updateTeam }
                                   team={ team }
                                   isTeamLoading={ teamLoading }
                                   errorHandler={ this.setError }
                        />
                    </Router>
                </div>
                { error !== null && <Error error={ error } closeError={ this.closeError }/> }
            </ErrorsContext.Provider>
        );
    }

    updateTeam = (team: Team) => {
        try {
            window.localStorage.setItem(API_KEY, team.apiKey);
        } catch (e) {
            /*...*/
        }
        
        this.setState({
            apiKey: team.apiKey,
            team
        });
    }

    deleteApiKey = () => {
        try {
            window.localStorage.removeItem(API_KEY);
        } catch (e) {
            /*...*/
        }
        this.setState({
            apiKey: null,
            team: null,
        });
    }

    closeError = () => {
        this.setState({ error: null });
    }

    setError = (err: string | Error) => {
        this.setState({ error: err });
        console.error(err);
    }
}

export default App;
