import { API_KEY } from '../consts/strings';
const json = 'application/json';

type ErrorHandler = (err: Error | string) => void;

export function sendGetRequest(url: string, errorHandler: ErrorHandler, withParams = false, addAuth = true) {
    const urlAuth = addAuth ? createAuthUrl(url, withParams) : url;
    
    return sendRequest(urlAuth, {}, errorHandler);
}

export function sendPostRequest(url: string, body: object, errorHandler: ErrorHandler, withParams = false, addAuth = true) {
    const urlAuth = addAuth ? createAuthUrl(url, withParams) : url;

    return sendRequest(urlAuth, {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json'
        },
        body: JSON.stringify(body),
    }, errorHandler)
}

export function sendPostFormRequest(url: string, body: string, errorHandler: ErrorHandler) {
    return sendRequest(url, {
        method: 'POST',
        headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
        },
        body: body,
    }, errorHandler)
}

function sendRequest(url: string, params: RequestInit, errorHandler: ErrorHandler) {
    return fetch(url, params).then(resp => {
        const chunked = resp.headers.get('Transfer-Encoding') === 'chunked';
        const len = parseInt(resp.headers.get('Content-Length') || '0', 10);
        const hasBody = len > 0 || chunked;
        const contentType = resp.headers.get('Content-Type')?.split(';')[0];

        if (resp.ok && contentType === json && hasBody) {
            return resp.json();
        }

        if (resp.ok) {
            return;
        }
        
        if (hasBody && contentType === json) {
            resp.json().then(res => errorHandler(res.message || res.error || JSON.stringify(res)))
            return Promise.reject('Error from server');
        }
        
        const errMessage = `Something went wrong. Server answer is ${ resp.status }${ resp.statusText ? ': ' + resp.statusText : ''}`
        errorHandler(errMessage);
        return Promise.reject(errMessage);
        
    }, errorHandler);
}

function createAuthUrl(url: string, withParams: boolean) {
    let apiKey;
    try {
        apiKey = window.localStorage.getItem(API_KEY);
    } catch (e) {
        /*...*/
    }
    if (!apiKey) return url;

    const keyInQuery = `apiKey=${ apiKey }`;
    const urlAuth = `${ url }${ withParams ? '&' : '?' }${ keyInQuery }`;

    return urlAuth;
}
