import React, { ChangeEvent, FormEvent, ReactElement } from 'react';
import styles from './styles.module.css';
import createFormData from '../../utils/createFormData';
import { sendPostFormRequest } from '../../utils/sendRequest';
import { registration } from '../../consts/api';
import Team from '../../models/Team';
import { ErrorsContext } from '../App/App';
import { ErrorHandler } from '../../models/Handlers';
import { Modal, Tooltip } from '@skbkontur/react-ui';
import { ROUTES } from '../../consts/routes';

type TeamInfo = {
    teamName: string;
    gitRepositoryUrl: string;
    customData_country: string;
    customData_peopleAmount: string;
    customData_email: string;
}

type Validations = { [key: string]: string };

type RegistrationPageProps = {
    history: {
        push: (url: string) => void;
    };
    updateTeam: (team: Team) => void;
}

type RegistrationPageState = {
    teamInfo: TeamInfo,
    validations: Validations;
    loading: boolean;
    requestResult?: Team;
}

export default class RegistrationPage extends React.Component<RegistrationPageProps, RegistrationPageState> {
    constructor(props: RegistrationPageProps) {
        super(props);

        this.state = {
            teamInfo: {
                teamName: '',
                gitRepositoryUrl: '',
                customData_country: 'World',
                customData_peopleAmount: '',
                customData_email: '',
            },
            validations: {},
            loading: false,
        }
    }

    render() {
        const { loading } = this.state;

        return (
            <div>
                <h2>Early Team Registration</h2>
                <div className={ styles.disclaimer }>
                    <p>We encourage to register your team before the start of the contest.</p>
                    <p>You'll be able to check the <a href="https://github.com/icfpcontest2020/dockerfiles/blob/master/README.md" target="_blank" rel="noopener noreferrer">submission system</a> and make sure that everything goes smoothly during the contest.</p>
                    <p>As part of registration, we kindly ask to create a Git repository for your team and provide its SSH URL.</p>
                    <p>Don't worry, all information you enter here can be updated later.</p>
                </div>
                <ErrorsContext.Consumer>{ ({ addError }) =>
                    <form className={ styles.form } onSubmit={ this.registerTeam(addError) }>
                        { this.renderRow('Team name *', 'teamName', true, { autoFocus: true, maxLength: 100 }) }
                        { this.renderRow('Contact email *', 'customData_email', true, {
                            type: 'email',
                            maxLength: 100
                        }) }
                        { this.renderGitField() }
                        { this.renderCountrySelect() }
                        { this.renderRow('Team size', 'customData_peopleAmount',
                            false, { type: 'number', maxLength: 100 }) }
                        <label className={ styles.buttonRow }>
                            <span/>
                            <div>
                                <button type='submit' disabled={ loading }>
                                    { loading ? "Registering..." : "Register" }
                                </button>
                            </div>
                        </label>
                    </form>
                }
                </ErrorsContext.Consumer>
            </div>
        )
    }

    renderRow(label: string | ReactElement, fieldName: keyof TeamInfo, required = false, props?: Object) {
        const { validations } = this.state
        const isError = fieldName in validations;

        return (
            <label className={ styles.row }>
                <span>{ label }</span>
                <span style={ { width: '100%' } }>
                    <Tooltip render={ () => <span className={styles.blackText}>{validations[fieldName]}</span> }
                             pos='right middle'
                             closeButton={ false }
                             trigger={ isError ? 'opened' : 'closed' }>
                        <input className={ isError ? ' ' + styles.error : '' }
                               name={ fieldName }
                               required={ required }
                               onChange={ this.onChange(fieldName) }
                               { ...props }
                        />
                    </Tooltip>
                </span>
            </label>
        );
    }

    renderGitField() {
        const { validations } = this.state
        const isError = 'gitRepositoryUrl' in validations;

        return (
            <label className={ styles.row }>
                <span>
                    Repository SSH URL *
                </span>
                <span style={ { width: '100%' } }>
                    <Tooltip render={ () =><span className={styles.blackText}>{validations['gitRepositoryUrl']}</span> }
                             pos='right middle'
                             closeButton={ false }
                             trigger={ isError ? 'opened' : 'closed' }>
                        <input className={ isError ? styles.error : '' }
                               name='gitRepositoryUrl'
                               required
                               onChange={ this.onChange('gitRepositoryUrl') }
                               maxLength={ 1024 }
                               placeholder="git@example.com:user/repo.git"
                        />
                    </Tooltip>
                </span>
            </label>
        );
    }

    renderCountrySelect() {
        return (
            <label className={ styles.row }>
                <span>Your country</span>
                <select name='customData_country'
                        onChange={ this.onChange('customData_country') }>
                    { getCountries().map(country =>
                        <option value={ country.value } key={ country.value }>{ country.text }</option>
                    ) }
                </select>
            </label>
        )
    }

    onChange = (field: keyof TeamInfo) => (e: ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
        const val = e.target.value;
        this.setState({
            teamInfo: {
                ...this.state.teamInfo,
                [field]: val,
            }
        })
    }

    onCloseRegisterModal = () => {
        this.props.history.push(ROUTES.teamInfo);
    }

    registerTeam = (errorHandler: ErrorHandler) => async (e: FormEvent) => {
        e.preventDefault();

        if (!this.validate()) return;

        this.setState({ loading: true })
        const body = createFormData(this.state.teamInfo);
        try {
            const result = await sendPostFormRequest(registration, body, errorHandler);
            this.setState({ requestResult: result });
            this.props.updateTeam(result);
        } catch (e) {
            errorHandler(e);
        } finally {
            this.setState({ loading: false });
        }
    }

    validate() {
        const { teamName, gitRepositoryUrl } = this.state.teamInfo;

        const validations: Validations = {};
        if (!teamName) {
            validations['teamName'] = 'Team Name is required for registration';
        }

        if (!gitRepositoryUrl) {
            validations['gitRepositoryUrl'] = 'Repository SSH URL is required for registration';
        }

        if (!/[^@]+@[^@:]+/.test(gitRepositoryUrl)) {
            validations['gitRepositoryUrl'] = 'Make sure the url you enter is a valid SSH URL';
        }

        this.setState({ validations })

        return Object.keys(validations).length === 0;
    }
}

