import React from 'react';
import styles from './styles.module.css';
import Team from '../../models/Team';

type TeamInfoPageProps = {
    team: Team | null;
}

export default function TeamInfoPage(props: TeamInfoPageProps) {
    const { team } = props;

    if (!team) return null;

    return (
        <main>
            <h2>Team profile: { team.teamName }</h2>
            <div>
                { renderRow('Team name', team.teamName) }
                { renderRow('Contact email', team.customData.email, true) }
                { renderRow('Repository SSH URL', team.gitRepositoryUrl, true) }
                { renderRow('Country', team.customData.country, true) }
                { renderRow('Team size', team.customData.peopleAmount, true) }
            </div>
            <br/>
            <div>
                { renderRow('API key', team.apiKey) }
                { renderRow('Deploy key', team.deployKey) }
            </div>
            <br/>
            <p className={ styles.disclaimer }>Beware not to lose your API key. Store it safely.</p>
            <p className={ styles.disclaimer }>Use API key:
                <ul>
                    <li>to log in to the team profile (this page)</li>
                    <li>to send requests to Organizers' server</li>
                </ul>
            </p>
            <p className={ styles.disclaimer }>Add deploy key to the CI settings of your repository as per <a href="https://github.com/icfpcontest2020/dockerfiles/blob/master/README.md" target="_blank" rel="nofollow noreferrer">submission rules</a>.</p>

        </main>
    )
}

function renderRow(label: string, value?: string, editable = false) {
    if (!value) return;
    
    return (
        <div className={ styles.row }>
            <b>{ label }:</b>
            <code className={ styles.value }>{ ' ' }{ value }</code>
        </div>
    )
}
