import React from 'react';
import styles from './styles.module.css';
import Team from '../../models/Team';
import Submission from '../../models/Submission';
import { sendGetRequest } from '../../utils/sendRequest';
import { submissions } from '../../consts/api';
import DateTime from '../DateTime';
import { ErrorHandler } from '../../models/Handlers';

type SubmissionsPageProps = {
    team: Team | null;
    errorHandler: ErrorHandler;
}

type SubmissionsPageState = {
    submissions: Submission[] | null;
}

export default class SubmissionsPage extends React.Component<SubmissionsPageProps, SubmissionsPageState> {
    constructor(props: SubmissionsPageProps) {
        super(props);
        this.state = {
            submissions: null,
        }
    }

    componentDidMount() {
        this.getSubmissions();
    }


    render() {
        const { team } = this.props

        if (!team) return null

        return (
            <div>
                <h2>Team submissions: { team.teamName }</h2>
                { this.renderSubmissions() }
            </div>
        )
    }

    renderSubmissions() {
        const { submissions } = this.state;
        return (
            <table className={ styles.table }>
                <thead>
                <tr>
                    <th>#</th>
                    <th>Commit Message</th>
                    <th>Created At</th>
                    <th>Build Status</th>
                    <th>Build Logs</th>
                    <th>Test Status</th>
                    <th>Test Logs</th>
                </tr>
                </thead>
                { submissions && submissions.map(s => this.renderRow(s)) }
            </table>
        )
    }

    renderRow(s: Submission) {
        const { team } = this.props;
        return (
            <tr>
                <td>{ s.commitHash?.substring(0, 6) }</td>
                <td><span className={ styles.pre }>{ s.commitMessage }</span></td>
                <td><DateTime dateStr={ s.createdAt }/></td>
                <td>{ s.buildStatus }</td>
                <td>
                    { s.buildLogKey && team &&
                    <a target='_blank'
                       className={ styles.logLink }
                       href={ `https://icfpc2020-api.testkontur.ru/logs/?logKey=${ encodeURIComponent(s.buildLogKey) }&apiKey=${ team.apiKey }` }>
                        View
                    </a> }
                </td>
                <td>
                    { s.testInfo?.testStatus }
                </td>
                <td>
                    { s.testInfo?.debugLogKey && team &&
                    <a target='_blank'
                       className={ styles.logLink }
                       href={ `https://icfpc2020-api.testkontur.ru/logs/?logKey=${ encodeURIComponent(s.testInfo.debugLogKey) }&apiKey=${ team.apiKey }` }>
                        View
                    </a> }
                </td>
            </tr>
        )
    }

    getSubmissions = async () => {
        const { errorHandler } = this.props;
        const teamSubmissions = await sendGetRequest(submissions, errorHandler);
        this.setState({ submissions: teamSubmissions });
    }
}
