import React, { ReactComponentElement } from 'react';
import { Route, Switch, Redirect } from 'react-router-dom';
import LandingPage from '../LandingPage';
import { ROUTES } from '../../consts/routes';
import RulesPage from '../RulesPage';
import RegistrationPage from '../RegistrationPage';
import LoginPage from '../LoginPage';
import Team from '../../models/Team';
import TeamInfoPage from '../TeamInfoPage';
import SubmissionsPage from '../SubmissionsPage';
import { ErrorHandler } from '../../models/Handlers';

type AppRouterProps = {
    updateTeam: (team: Team) => void;
    team: Team | null;
    isTeamLoading: boolean;
    errorHandler: ErrorHandler;
}

export default function AppRouter(props: AppRouterProps) {
    const { updateTeam, team, isTeamLoading, errorHandler } = props;
    return (
        <Switch>
            <Route exact path='/' component={ LandingPage }/>
            <Route path={ ROUTES.rules } component={ RulesPage }/>
            <Route path={ ROUTES.registration } children={ ({ history }) => {
                const component = <RegistrationPage history={ history } updateTeam={ updateTeam } />;
                return forUnauthorized(team, component)
            } }/>
            <Route path={ ROUTES.login } children={ ({ history }) => {
                const component = <LoginPage history={ history } updateTeam={ updateTeam }/>;
                return forUnauthorized(team, component)
            } }/>
            <Route path={ ROUTES.teamInfo } children={ () => {
                const component = <TeamInfoPage team={ team }/>;
                return forAuthorized(team, isTeamLoading, component);
            } }/>
            <Route path={ ROUTES.submissions } children={ () => {
                const component = <SubmissionsPage team={ team } errorHandler={ errorHandler }/>;
                return forAuthorized(team, isTeamLoading, component);
            } }/>
        </Switch>
    )
}

function forUnauthorized(team: Team | null, component: ReactComponentElement<any>) {
    if (team) {
        return <Redirect to={ ROUTES.teamInfo }/>
    }
    return component;
}


function forAuthorized(team: Team | null, loading: boolean, component: ReactComponentElement<any>) {
    if (!team && !loading) {
        return <Redirect to={ ROUTES.login }/>
    }
    return component;
}
